import React from "react";
import styled from "styled-components";

const topSectionContainer = styled.div`
  position: absolute;
  width: 200;
  height: 500;
  top: 0;
  left: 0;
  background-color: "white";
  z-index: 10;
`;

const Logo = styled.h1`
  margin: 0;
  color: white;
  font-weight: 400;
  top: 30px;
  left: 30px;
  font-size: 20px;
  padding: 10px;
  margin-left: 20px;
`;

function Explanation() {
  return (
    <topSectionContainer>
      <Logo> "@react-three/drei" Stars   </Logo>
    </topSectionContainer>
  );
}

export default Explanation;
